@import "../../assets/styles/theme/theme_variables";

:root {
	font-family: 'Sen', "helvetica",
		times,
		serif;
}

// Typography
$headings-font-family: inherit;
$headings-font-weight: 700;

$font-family-sans-serif: inherit;
$font-family-base: inherit;

$line-height-base: 1.4;

$gray-300: $gray-500;
$font-weight-base: 400;
$font-weight-normal: $font-weight-base;
$font-weight-bold: 700;

// Colors
$primary: #00d082;
$secondary: #02A665;
$success: #21a77a;
$info: #f7f7f9;
$warning: #ec971f;
$danger: #843534;
$dark: #000000;
$light: #ffffff;

$link-hover-color: #4f4f4f;
$link-color: #121212;

$gray-500: #d2d2d2;
$gray-600: rgba(151, 151, 151, 1);
$gray-700: #2b2b2b;
$gray-800: #767676;
$gray-900: #4a4a4a;

$body-color: #4a4a4a;
